/**
 * prism.js tomorrow night eighties for JavaScript, CoffeeScript, CSS and HTML
 * Based on https://github.com/chriskempson/tomorrow-theme
 * @author Rose Pritchard
 */
code[class*="language-"],
pre[class*="language-"] {
  color: #ccc;
  background: none;
  font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  line-height: 1.5;
  -moz-tab-size: 4;
  -o-tab-size: 4;
  tab-size: 4;
  -webkit-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
}
/* Code blocks */
pre[class*="language-"] {
  padding: 1em;
  margin: .5em 0;
  overflow: auto;
}
:not(pre) > code[class*="language-"],
pre[class*="language-"] {
  background: #3d3940;
}
/* Inline code */
:not(pre) > code[class*="language-"] {
  padding: .1em;
  border-radius: .3em;
  white-space: normal;
}
.token.comment,
.token.block-comment,
.token.prolog,
.token.doctype,
.token.cdata {
  color: #999;
}
.token.punctuation {
  color: #ccc;
}
.token.tag,
.token.attr-name,
.token.namespace,
.token.deleted {
  color: #e2777a;
}
.token.function-name {
  color: #6196cc;
}
.token.boolean,
.token.number,
.token.function {
  color: #f08d49;
}
.token.property,
.token.class-name,
.token.constant,
.token.symbol {
  color: #f8c555;
}
.token.selector,
.token.important,
.token.atrule,
.token.keyword,
.token.builtin {
  color: #cc99cd;
}
.token.string,
.token.char,
.token.attr-value,
.token.regex,
.token.variable {
  color: #7ec699;
}
.token.operator,
.token.entity,
.token.url {
  color: #67cdcc;
}
.token.important,
.token.bold {
  font-weight: bold;
}
.token.italic {
  font-style: italic;
}
.token.entity {
  cursor: help;
}
.token.inserted {
  color: green;
}
.nav-logo {
  display: inline-block;
}
.nav-logo img {
  height: 48px;
}
.nav {
  background: #fefefe;
  height: 72px;
  border-bottom: 1px solid rgba(230, 230, 230, 0.99);
  z-index: 2;
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 99999;
}
.nav header {
  position: relative;
  padding: 12px 0 12px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 75%;
  min-width: 1000px;
  margin: 0 auto;
}
.nav header .entry-list {
  position: absolute;
  right: 36px;
  top: 0;
  bottom: 0;
  line-height: 72px;
}
.nav header .entry-list a.nav-entry {
  margin: 0 12px;
  color: #9260B7;
  font-size: 20px;
}
ul,
li,
dl,
dt,
dd {
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
  background-color: transparent;
}



