html,
body {
  height: 100%;
}
body {
  margin: 0;
}
.layout {
  padding-top: 56px;
}
.layout .main-content {
  width: 75%;
  min-width: 1000px;
  margin: 0px auto 0px;
  min-height: 200px;
  overflow: hidden;
  background: #FFFFFF;
  border-radius: 2px;
}
.layout .main-content article {
  min-height: 400px;
  padding: 16px 50px 50px 260px;
}
.layout .main-content .content-body {
  color: #585858;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 16px;
  word-wrap: break-word;
  line-height: 2;
  letter-spacing: 0.2px;
}



